@import url("https://fonts.googleapis.com/css2?family=JetBrains+Mono&display=swap");

/* 
Extra small devices (portrait phones, less than 544px) 
No media query since this is the default in Bootstrap because it is "mobile first"
*/
body {
  -webkit-user-select: none; /* Safari */
  -moz-user-select: none; /* Firefox */
  -ms-user-select: none; /* IE10+/Edge */
  user-select: none; /* Standard */
  font-family: "JetBrains Mono", monospace;
}
#logoImg {
  height: 40px;
  cursor: pointer;
  -webkit-animation: spin 10s linear infinite;
  -moz-animation: spin 10s linear infinite;
  animation: spin 10s linear infinite;
}
#logoImg:active {
  -webkit-animation: pulse 0.2s linear;
  -moz-animation: pulse 0.2s linear;
  animation: pulse 0.2s linear;
}

#responsive-navbar-nav > div > a {
  cursor: pointer;
}

#projectImg {
  max-width: 400px;
}
#projectHeader {
  font-size: 1rem;
}
#projectDescription {
  font-size: 0.8rem;
} /*1rem = 16px*/

.tilty {
  display: inline-flex;
  width: 150px;
  height: 150px;
  border-radius: 20px;
  transform-style: preserve-3d;
  background-repeat: no-repeat;
  background-position: center;
}

.inner-element {
  display: flex;
  opacity: 1;
  transition: 0.4s;
  background: radial-gradient(
    circle,
    rgba(0, 0, 0, 0.5970763305322129) 0%,
    rgba(247, 247, 247, 0) 63%
  );
  flex-direction: column;
  justify-content: center;
  height: 100%;
  width: 100%;
  margin: auto;
  transform-style: preserve-3d;
  transform: translateZ(30px);
}
.inner-element.active {
  opacity: 1;
}
.inner-element div {
  display: block;
  font-size: 22px;
  font-style: italic;
  color: white;
}
#invMsg {
  opacity: 0;
  color: red;
}
/*1rem = 16px*/
/*
####################################################
M E D I A  Q U E R I E S
####################################################
*/

/*
::::::::::::::::::::::::::::::::::::::::::::::::::::
Bootstrap 4 breakpoints
*/
/* Small devices (landscape phones, 544px and up) */
@media (min-width: 544px) {
  #greetingHeader > span {
    font-size: 1.4rem;
  }
  #projectImg {
    max-width: 500px;
  }
  #projectHeader {
    font-size: 1.8rem;
  }
  #projectDescription {
    font-size: 1.2rem;
  } /*1rem = 16px*/
}

/* Medium devices (tablets, 768px and up) The navbar toggle appears at this breakpoint */
@media (min-width: 768px) {
  #greetingHeader > span {
    font-size: 1.4rem;
  }
  #projectHeader {
    font-size: 1.1rem;
  }
  #projectDescription {
    font-size: 0.9rem;
  } /*1rem = 16px*/
}

/* Large devices (desktops, 992px and up) */
@media (min-width: 992px) {
  #greetingHeader > span {
    font-size: 1.5rem;
  }
  #projectHeader {
    font-size: 1.6rem;
  }
  #projectDescription {
    font-size: 1.1rem;
  } /*1rem = 16px*/
  #responsive-navbar-nav > div > a {
    cursor: pointer;
    transition: 0.4s;
  }
  #responsive-navbar-nav > div > a:hover {
    color: #61dbfb;
    transition: 0.4s;
  }
  .active {
    color: #61dbfb !important;
    -webkit-animation: pulse 2s linear infinite;
    -moz-animation: pulse 2s linear infinite;
    animation: pulse 2s linear infinite;
  }
}

/* Extra large devices (large desktops, 1200px and up) */
@media (min-width: 1200px) {
  #greetingHeader > span {
    font-size: 1.8rem;
  }
  #projectHeader {
    font-size: 2rem;
  }
  #projectDescription {
    font-size: 1.2rem;
  } /*1rem = 16px*/
}

/*
::::::::::::::::::::::::::::::::::::::::::::::::::::
Custom media queries
*/

/*Animation*/
@-moz-keyframes spin {
  100% {
    -moz-transform: rotate(360deg);
  }
}
@-webkit-keyframes spin {
  100% {
    -webkit-transform: rotate(360deg);
  }
}
@keyframes spin {
  100% {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}

@-moz-keyframes pulse {
  0% {
    -moz-transform: scale(1);
  }
  50% {
    -moz-transform: scale(0.9);
  }
  100% {
    -moz-transform: scale(1);
  }
}
@-webkit-keyframes pulse {
  0% {
    -webkit-transform: scale(1);
  }
  50% {
    -webkit-transform: scale(0.9);
  }
  100% {
    -webkit-transform: scale(1);
  }
}
@keyframes pulse {
  0% {
    -webkit-transform: scale(1);
    transform: scale(1);
  }
  25% {
    -webkit-transform: scale(0.95);
    transform: scale(0.95);
  }
  50% {
    -webkit-transform: scale(0.9);
    transform: scale(0.9);
  }
  75% {
    -webkit-transform: scale(0.95);
    transform: scale(0.95);
  }
  100% {
    -webkit-transform: scale(1);
    transform: scale(1);
  }
}
